.candidate-bar{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  overflow-x: scroll;
  padding: 1%;
}

.candidate-img {
  background-position: center;
  background-size: cover;
  height: 100px;
  margin: 0 2px;
  min-width: 100px;
}

.candidate-img-div .dropdown-content {
  display:none;
}

.dropdown-content p {
  font-size: 12px;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}
.candidate-img-div:hover .dropdown-content{
  display:block;
}

.grow { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }

.grow:hover { -webkit-transform: scale(1.05); -ms-transform: scale(1.05); transform: scale(1.05); }


